function PivotColumn(columnKey) {
    this.columnKey = columnKey;
    this.sum = 0;
    this.entries = [];
    this.value = null;
}
PivotColumn.prototype.add = function(pivotEntry, value) {
    this.entries.push(pivotEntry);
    if (typeof value == 'number') {
        if (pivotEntry.isTotalLast) {
            if (pivotEntry.isLast) {
                this.sum += value;
            }
        } else {
            this.sum += value;
        }
    } else if (angular.isObject(value) && Object.keys(value).length > 0) {
        this.value = this.value || {};
        for (var key in value) {
            if (typeof value[key] == 'number') {
                if (pivotEntry.isTotalLast) {
                    if (pivotEntry.isLast) {
                        this.value[key] = value[key] + (this.value[key] || 0);
                    }
                } else {
                    this.value[key] = value[key] + (this.value[key] || 0);
                }
            }
        }
    }
};
PivotColumn.prototype.remove = function(pivotEntry, value) {
    for (var i = 0; i < this.entries.length; i++) {
        if (this.entries[i].worklog.id === pivotEntry.worklog.id) {
            this.entries.splice(i, 1);
            break;
        }
    }
    if (typeof value == 'number') {
        this.sum -= value;
    } else if (angular.isObject(value) && Object.keys(value).length > 0) {
        this.value = this.value || {};
        for (var key in value) {
            if (typeof value[key] == 'number') {
                this.value[key] = value[key] - (this.value[key] || 0);
            }
        }
    }
};